.hamburger {
	padding: 8px;
	transform: translateY(-5px);
	.hamburger-box {
		height: 0;
		width: 30px;
		.hamburger-inner {
			position: relative;

			&,
			&::after,
			&::before {
				width: 100%;
				height: 1px;
				border-radius: $border-radius;
				background-color: currentColor;
			}

			&::after,
			&::before {
				content: '';
				position: absolute;
				left: 0;
			}
		}
	}
	&.is-active {
		.hamburger-inner {
			background-color: transparent;
		}
	}
}
.hamburger-inner::before {
	top: -7px;
}
.hamburger--emphatic .hamburger-inner::after {
	top: 7px;
}